
.result {
  margin-left: 50px;
  margin-right: 50px;
  padding-top: 20px;
  border-top: 2px solid #ddd;
}

.result h3,
.result h4 {
  font-weight: normal;
}

.temperature {
  /* max-width: 50%; */
  height: 60vh;
  border-bottom: 1px solid #3f5c66;
  border-bottom-color: groove;
}
@media (max-width: 1000px) {
  .temperature {
    display: none;
  }
}

.App input,
.App {
  margin: 20px;
  padding: 10px 20px;
  border: 2 px solid black;
  font-family: Arial;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  color: #3f5c66;
}
.textAndImg {
  /* flex-wrap: wrap; */
  display: flex;
}

h1 {
  font-size: 5vh;
}
h2 {
  font-size: 3vh;
}
h3 {
  font-size: 2vh;
}
@media (max-width: 1000px) {
  body {
    text-align: center;
    background-image: url(/weather-react/static/media/temperature.0ba2046e.jpg);
    background-size: cover;
    background-position: center;
  }
}

